<template>
  <div class="part-five">
    <h3 class="text-center text-top">实践经历</h3>
    <p>
      <a href="javascript:void(0)">广州山雨科技有限公司</a>
    </p>
    <p class="text-small"> 2017/12 - 至今</p>
    <p class="text-small">工作内容：微信小程序、Vue + ElementUI管理后台……</p>
    <p class="text-small">项目成就：</p>
    <p class="text-small">&emsp;1. 人力资源小程序。使用原生代码进行微信小程序的开发，对表现与数据分离有了更深刻的了解。</p>
    <p class="text-small">&emsp;2. 青芒管理后台。运用Vue + ElementUI完成一个社团合集小程序的管理后台。</p>
    <p class="text-small">&emsp;3. 模仿拼多多移动端官网。用Vue + Vuex + node.js制作，并对flex布局有更深刻的了解.</p>
  </div>
</template>

<script>
export default {
  name: "partFive",
  data() {
    return {
      
    };
  },
  
};
</script>

<style scoped>
@-webkit-keyframes masked-animation {
    0% {
            background-position: 0 0
        }
    to {
            background-position: -100% 0
        }
}
@keyframes masked-animation {
    0% {
            background-position: 0 0
        }
    to {
            background-position: -100% 0
        }
}
a {
  color: deepskyblue;
}
a:hover {
  color: rgb(118, 190, 248);
}
.part-five {
  width: 100%;
  height: 450px;
  border: 40px solid transparent;
  border-image: url("~@/./assets/img/border_image.png") 30 30 stretch;
  background: #18202d;
  text-align: left;
}

.text-center {
    text-align: center;
    margin-bottom: 30px;
    color: hsla(0, 0%, 100%, .65);
    font-weight: 800;
    background-image: -webkit-linear-gradient(left, #00bfff, #207ffc 25%, #00eeff 50%, #0066ff 75%, #00ffdd);
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-background-size: 200% 100%;
    background-size: 200% 100%;
    -webkit-animation: masked-animation 2s infinite linear;
    animation: masked-animation 2s infinite linear;

}
.text-center:hover {
     -webkit-animation: masked-animation 0.5s infinite linear;
     animation: masked-animation 0.5s infinite linear;
     cursor: pointer;
}
.text-small {
  font-size: 0.9em;
  color: rgb(253, 239, 239);
  line-height: 30px;
}
</style>




